@media (max-width: 767px) {
  .footer__logo {
    --tabletLogo: none;
    --mobileLogo: block;
    width: 67px;
    height: 13px;
  }
}

@media (max-width: 767px) {
  .footer__nav {
    width: 100%;
  }
}

.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px 41px;
  max-width: 700px;
  margin: 0;
  padding: 0;
  padding-left: 12px;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .footer__nav-list {
    gap: 31px;
    max-width: 550px;
  }
}
@media (max-width: 767px) {
  .footer__nav-list {
    display: block;
    max-width: unset;
    column-count: 2;
    column-gap: 30px;
    padding: 0;
  }
}

@media (max-width: 767px) {
  .footer__nav-list li {
    margin-top: 16px;
  }
}
@media (max-width: 767px) {
  .footer__nav-list li:first-of-type {
    margin-top: 39px;
  }
}

.footer__nav-list a {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .footer__nav-list a {
    font-size: 16px;
    line-height: 19px;
  }
}
.footer__nav-list a:hover {
  opacity: 0.6;
}
.footer__nav-list a:focus-visible {
  outline: 2px solid #ffffff;
  outline-offset: 4px;
}
.footer__nav-list a:active {
  opacity: 0.3;
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 11px;
  max-width: 200px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .footer__social-list {
    margin-right: -6px;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    justify-content: flex-start;
    max-width: unset;
  }
}

.footer__social-link {
  display: block;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: 25px 25px;
  transition: opacity 0.3s ease;
}
.footer__social-link--vk {
  background-image: url("../img/svg/vk.svg");
}
.footer__social-link--ok {
  background-image: url("../img/svg/ok.svg");
}
.footer__social-link--reddit {
  background-image: url("../img/svg/reddit.svg");
}
.footer__social-link:hover {
  opacity: 0.6;
}
.footer__social-link:focus-visible {
  outline: 2px solid #ffffff;
  outline-offset: 4px;
}
.footer__social-link:active {
  opacity: 0.3;
}

